import { actions } from "mirrorx";
import * as api from "./service";

export default {
    //  Store 
    name: "order",
    //  Model  state
    initialState: {
        order: '',
    },
    reducers: {
        /**
         *  Redux  Reducer
         * @param {*} state
         * @param {*} data
         */
        updateState(state, data) { //state
            return {
                ...state,
                ...data
            };
        }
    },
    effects: {
        /**
         * 
         * @param {*} param
         * @param {*} getState
         */
        async loadData(params, getState) {
            let result = await api.getList(params);
            return result;
        }
    }
};
